h1,
p,
button {
  color: $otherTheme-1;
}

nav {
  position: fixed;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  width: 100vw;
  height: $nav-height;
  background-color: $overallTheme-2;

  #nav-logo {
    position: relative;
    left: 20px;
    display: inline-flex;
    align-items: center;
    cursor: pointer;

    &::before {
      position: absolute;
      left: -20px;
      z-index: -1;
      width: 0%;
      height: 100%;
      background-color: $overallTheme-1;
      transition-duration: 0.8s;
      content: "";
    }

    &.select,
    &.hover {
      &::before {
        width: 120%;
      }
    }

    #nav-logo__heading {
      display: flex;
      align-items: flex-start;
      flex-direction: column;

      h1,
      p {
        font-style: italic;
        font-family: "Noto Sans TC 700", sans-serif;
      }

      h1 {
        font-size: 34px;
      }

      p {
        position: relative;
        font-size: 21px;
        top: -10px;
      }
    }
  }

  #nav__options-group {
    display: flex;

    .nav__options-title {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 120px;
      cursor: pointer;

      &::before {
        position: absolute;
        bottom: 0;
        z-index: -1;
        width: 100%;
        height: 0%;
        background-color: $overallTheme-1;
        transition-duration: 0.5s;
        content: "";
      }

      &.select,
      &.hover {
        &::before {
          height: 100%;
        }
      }

      .nav__options-title--surface {
        position: absolute;
        font-size: 1.1rem;
        font-family: "Noto Sans TC 400", sans-serif;
        transition-duration: 0.6s;
        transition-timing-function: ease-out;
        &.select,
        &.hover {
          opacity: 0;
        }
      }

      .nav__options-title--inside {
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: "Cascadia Mono 400", sans-serif;

        span {
          opacity: 0;
          transition-duration: 0.4s;
          transition-timing-function: ease-out;
        }

        &.select,
        &.hover {
          span {
            opacity: 1;
            transition-duration: 0.6s;
            transition-timing-function: ease-in;
          }

          .button__code-symbol {
            color: $otherTheme-1;
          }

          .button__code-string {
            color: $otherTheme-2;
          }
        }
      }
    }
  }
}
