h2 {
  font-size: 1.4rem;
}

h3 {
  font-size: 1.1rem;
}

%show-animation {
  position: relative;
  top: 30px;
  opacity: 0;
  transition-duration: 0.8s;
  transition-timing-function: ease-out;
  &.selected {
    top: 0px;
    opacity: 1;
  }
}

main {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 9600px;
  transition-duration: 0.8s;

  .nav-height {
    height: $nav-height;
  }

  #main-home,
  #main-skill,
  #main-project1,
  #main-project2,
  #main-work {
    width: 1920px;
    height: 100%;
  }

  #main-whole {
    display: flex;
    align-items: center;
    width: 100%;
    height: $remaining-height;

    #main-home {
      display: flex;
      justify-content: center;
      align-items: center;

      #main-home__whole {
        position: absolute;
        width: 13%;
        height: 90%;

        .main-home__half {
          position: absolute;
          display: flex;
          align-items: center;
          width: 100%;
          height: 55%;

          .main-home__quarter {
            @extend %show-animation;
            display: flex;
            width: 100%;
          }

          .main-home__img {
            justify-content: center;
            height: 70%;
            img {
              border-radius: 1rem;
            }
          }

          .main-home__article {
            flex-direction: column;
            p {
              margin: 0.5rem;
            }
          }
        }

        #main-home__half--top {
          top: 0px;
        }

        #main-home__half--bottom {
          bottom: 0px;
          .main-home__article {
            align-items: flex-end;
          }
        }
      }
    }

    #main-skill {
      display: flex;
      justify-content: center;
      align-items: center;

      #main-skill__whole {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        width: 40%;
        height: 50%;

        #main-skill__half-1 {
          @extend %show-animation;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          flex-direction: column;
          width: 100%;
          background-color: $otherTheme-3;
          border: solid 3px white;
          border-radius: 1rem;

          hr,
          ul {
            width: 90%;
            margin: 0.5rem 0;
          }

          h2 {
            text-align: center;
          }

          ul {
            display: flex;
            justify-content: space-between;

            li {
              width: calc(calc(100% / 4) - 2rem);
              padding: 0.5rem;
              background-color: $overallTheme-1;
              border-radius: 2rem;
              text-align: center;
            }
          }
        }

        #main-skill__half-2 {
          @extend %show-animation;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          width: 100%;

          hr,
          #main-skill__square-group {
            width: 100%;
            margin: 0.5rem 0;
          }

          h2 {
            text-align: center;
          }

          #main-skill__square-group {
            display: flex;
            justify-content: space-between;

            .main-skill__square--small {
              display: flex;
              align-items: center;
              flex-direction: column;
              width: calc(calc(100% / 4) - 1.5rem);
              background-color: $overallTheme-2;
              border: solid 3px white;
              border-radius: 1rem;

              h3 {
                margin: 0 0 1rem 0;
              }

              ul {
                display: flex;
                align-items: center;
                flex-direction: column;
              }
            }
          }
        }
      }
    }

    #main-project1 {
      display: flex;
      justify-content: center;
      align-items: center;

      #main-project1__whole {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 65%;
        height: 55%;

        #main-project1__img-preview {
          @extend %show-animation;
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-direction: column;
          height: 500px;

          button {
            width: 150px;
            height: 50px;
            color: $overallTheme-1;
            background-color: $overallTheme-2;
            border: solid 3px $overallTheme-2;
            font-size: 1.1rem;
            font-family: "Noto Sans TC 500", sans-serif;
            transition-duration: 0.2s;
            cursor: pointer;
            &:hover {
              color: $overallTheme-2;
              background-color: $overallTheme-1;
            }
          }
        }

        #main-project1__illustrate {
          @extend %show-animation;
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          width: 450px;
          height: 450px;
          padding: 1rem;
          background-color: $otherTheme-3;
          border: solid 3px $otherTheme-1;
          border-radius: 1.5rem;

          h2 {
            width: 100%;
            margin: 0 0 1rem 0;
            text-align: center;
          }

          div {
            hr {
              width: 4rem;
              margin: 0.3rem 0;
              border: solid 2px $otherTheme-1;
            }

            span {
              color: $otherTheme-2;
              font-size: 1.1rem;
              font-family: "Noto Sans TC 500", sans-serif;
            }

            a {
              display: inline-block;
              margin: 5px;
              width: 64px;
              height: 64px;
              background-color: $otherTheme-1;
              border-radius: 0.5rem;
              cursor: pointer;
            }
          }
        }
      }
    }

    #main-project2 {
      display: flex;
      justify-content: center;
      align-items: center;

      #main-project2__whole {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 50%;
        height: 45%;

        #main-project2__img-preview {
          @extend %show-animation;
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-direction: column;
          height: 420px;

          img {
            border-radius: 1rem;
          }

          button {
            width: 120px;
            height: 40px;
            color: $otherTheme-1;
            background-color: $otherTheme-2;
            border-radius: 0.5rem;
            font-size: 1.1rem;
            font-family: "Noto Sans TC 500", sans-serif;
            transition-duration: 0.2s;
            cursor: pointer;
          }
        }
        #main-project2__illustrate {
          @extend %show-animation;
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          width: 450px;
          height: 450px;
          padding: 1rem;
          background-color: $otherTheme-3;
          border: solid 3px $otherTheme-1;
          border-radius: 1.5rem;

          h2 {
            width: 100%;
            text-align: center;
          }

          h4 {
            width: 100%;
            margin: 0 0 1rem 0;
            text-align: center;
          }

          div {
            hr {
              width: 4rem;
              margin: 0.3rem 0;
              border: solid 2px $otherTheme-1;
            }

            span {
              color: $otherTheme-2;
              font-size: 1.1rem;
              font-family: "Noto Sans TC 500", sans-serif;
            }

            a {
              display: inline-block;
              margin: 5px;
              width: 64px;
              height: 64px;
              background-color: $otherTheme-1;
              border-radius: 0.5rem;
              cursor: pointer;
            }
          }
        }
      }
    }

    #main-work {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      #main-work__whole {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          opacity: 0.2;
        }

        #main-work__illustrate-group {
          position: absolute;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          flex-direction: column;
          width: 35%;
          height: 60%;

          .main-work__illustrate {
            @extend %show-animation;
            width: 100%;

            li {
              margin: 0 0 0 0.5rem;
              color: $otherTheme-4;
              list-style: inside square;
            }
          }
        }
      }
    }
  }
}
