// 導入字體
@each $font,
  $url
    in (
      "Noto Sans TC 400": "./fonts/NotoSansTC-Regular.ttf",
      "Noto Sans TC 500": "./fonts/NotoSansTC-Medium.ttf",
      "Noto Sans TC 700": "./fonts/NotoSansTC-Bold.ttf",
      "Cascadia Mono 400": "./fonts/CascadiaMono-Regular.ttf"
    )
{
  @font-face {
    font-family: "#{$font}";
    src: url(#{$url});
  }
}

$nav-height: 70px;
$remaining-height: calc(100vh - $nav-height);

$overallTheme-1: #2c3e50;
$overallTheme-2: #8aa8b7;
$otherTheme-1: #ffffff;
$otherTheme-2: #3498db;
$otherTheme-3: #4f2b1e;
$otherTheme-4: #ffd5ab;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
li {
  color: $otherTheme-1;
  font-weight: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  font-family: "Noto Sans TC 500", sans-serif;
}

p,
li {
  font-family: "Noto Sans TC 400", sans-serif;
}

li {
  list-style-type: none;
}

button {
  border: none;
  background: none;
}

body {
  background: $overallTheme-1;
}

@import "./nav";
@import "./main";
